.newsletter {
    background-color: #F4F4F2;
    margin-top: 7.5vh;
    padding: 10vh 0;
    text-align: center;
    .newsletter-heading {
        font-weight: 400;
        font-size: 4.8rem;
        line-height: calc(4.8rem * 1.3);
        word-spacing: 7.5px;
    }
    .newsletter-subheading {
        font-size: 1.3rem;
        width: 33.33333%;
        margin: 1.1rem auto 0;
        line-height: calc(1.3rem * 1.5);
    }
    article {
        width: 40%;
        margin: 2.5vh auto 10vh;
        .newsletter-form {
            margin: 6.5vh auto 1.5rem;
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            align-items: center;
            input {
                margin-right: 1.5rem;
                width: 70%;
                outline: none;
                border: 1px solid rgb(200, 200, 200);
                background: #fff;
                padding: 1rem;
                font-size: 1rem;
                height: 3.5rem;
                &::placeholder {
                    color: #000;
                }
            }
            button {
                height: 3.5rem;
                background-color: #000;
                color: #fff;
                font-weight: 400;
                font-size: 0.9rem;
                border: none;
                outline: none;
                text-transform: uppercase;
                display: flex;
                align-items: center;
                cursor: pointer;
                padding: 1rem 1.5rem;
                transition: .15s ease-in-out;
                letter-spacing: 0.25px;
                word-spacing: 2px;
                .arrow-icon {
                    color: rgb(195, 139, 33);
                    font-size: 1.5rem;
                    margin-left: 1.2rem;
                }
                &:hover {
                    background-color: rgb(17, 17, 17);
                }
            }
        }
        .newsletter-privacy_policy {
            display: flex;
            input[type=checkbox] {
                display: flex;
                align-self: start;
                margin-right: 1.75rem;
                position: relative;
                cursor: pointer;
            }
            input[type=checkbox]:before {
                content: "";
                display: block;
                position: absolute;
                width: 25px;
                height: 25px;
                top: 0;
                left: 0;
                border: 1px solid orange;
                background-color: white;
            }
            input[type=checkbox]:checked:after {
                content: "";
                display: block;
                width: 7px;
                height: 12px;
                border: solid orange;
                border-width: 0 2px 2px 0;
                -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
                transform: rotate(45deg);
                position: absolute;
                top: 4px;
                left: 10px;
            }
            p {
                text-align: left;
                font-size: 0.875rem;
                line-height: 1.225rem;
                text-wrap: pretty;
                white-space: normal;
            }
        }
    }
    .articles-heading {
        font-size: 0.925rem;
        color: rgb(110, 110, 110);
    }
    .highlighted-articles {
        width: 85%;
        margin-top: 5vh;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .top-article {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-family: Arial, Helvetica, sans-serif;
            width: 22.5%;
            border-left: 4px solid rgb(234, 155, 59);
            font-size: 1.45rem;
            line-height: 40px;
            font-weight: 600;
            text-align: left;
            padding-left: 22px;
            font-style: italic;
            img {
                max-width: 32.5%;
                min-width: 37.5%;
                width: 35%;
                padding-top: 2.5rem;
                margin-left: 1rem;
                margin-bottom: 0.25rem;
            }
        }
    }
}