.orderVehicle-section {
    margin-top: 17.5vh;
    article {
        h1 {
            font-size: 5.25rem;
            font-weight: 400;
            letter-spacing: 1px;
            text-align: center;
        }
        p {
            font-size: 1.2rem;
            margin: 1rem auto;
            text-align: center;
            width: 35%;
            letter-spacing: 0.25px;
            line-height: calc(1.2rem * 1.3);
        }
        button {
            margin: 4rem auto 4.5rem;
            background-color: black;
            color: white;
            font-weight: 500;
            border: none;
            outline: none;
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
            cursor: pointer;
            padding: 1.05rem 2rem;
            transition: 0.13s ease-in-out;
            &:hover {
                background-color: rgb(35, 35, 35);
            }
            .arrow-icon {
                transform: rotate(90deg);
                font-size: 1.325rem;
                margin-left: 0.8rem;
                color: rgb(195, 139, 33);
            }
        }
    }
    .orderVehicle-thumbnail {
        width: 100%;
        height: 65vh;
        filter: brightness(0.7);
        object-position: top;
        object-fit: cover;
    }
}

.orderVehicle-form {
    margin: 10vh 0;
    h1 {
        font-size: 5rem;
        font-weight: 400;
        letter-spacing: 1px;
        text-align: center;
    }
    .form-container {
        width: 85%;
        margin: 10vh auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        video {
            border-radius: 32px;
            width: 50%;
            height: auto;
            filter: brightness(0.7);
        }
        .form-info-wrapper {
            width: 45%;
            margin-left: 10%;
            .form-info {
                margin: 0 0 2.3rem;
                width: 90%;
                h4 {
                    font-size: 1.1rem;
                    font-weight: 600;
                    letter-spacing: -0.2px;
                    margin-bottom: 0.25rem;
                }
                p {
                    color: rgb(98, 98, 96);
                    letter-spacing: 0.1px;
                    display: flex;
                    align-items: center;
                }
                h3 {
                    font-size: 1.55rem;
                    color: rgb(40, 40, 40);
                    font-weight: 400;
                    margin-bottom: 0.25rem;
                }
            }
            .form-info-price {
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                .icon-info {
                    margin-left: 0.3rem;
                }
                .full_price {
                    color: #000;
                    font-size: 1.05rem;
                    font-weight: 700;
                }
                .todayPay-price {
                    color: rgb(25, 25, 25);
                    font-size: 2rem;
                }
            }
            .final-payment {
                width: 85%;
                border-radius: 0.75rem;
                background-color: rgb(244, 244, 241);
                p {
                    font-size: 0.875rem;
                    line-height: 15.5px;
                    padding: 0.75rem;
                    padding-left: 1.25rem;
                    padding-right: 5rem;
                }
            }
            button {
                margin: 1.75rem 0;
                background-color: black;
                color: white;
                font-weight: 500;
                border: none;
                outline: none;
                display: flex;
                justify-content: center;
                align-items: center;
                text-transform: uppercase;
                cursor: pointer;
                padding: 1.15rem 1.75rem;
                transition: 0.12s linear;
                &:hover {
                    background-color: rgb(32, 32, 32);
                }
                .arrow-icon {
                    font-size: 1.325rem;
                    margin-left: 0.8rem;
                    color: rgb(195, 139, 33);
                }
            }
            .button-subheading {
                margin: 1rem 0;
                font-size: 0.9rem;
                line-height: 15px;
                width: 85%;
                span {
                    cursor: pointer;
                    color: rgb(255, 153, 0);
                    text-decoration: underline;
                }
            }
        }
    }
}