footer {
    color: rgb(125, 125, 125);
    width: 85%;
    margin: 5vh auto 7.5vh;
    .footer-wrapper {
        border-top: 1px solid rgb(200, 200, 200);
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 1.25rem;
        margin-bottom: 1.5rem;
        .logo {
            user-select: none;
            cursor: pointer;
            height: 8vh;
            width: auto;
            object-fit: contain;
        }
        .footer-address {
            margin-top: 1.5rem;
            display: flex;
            flex-direction: column;
            font-size: 0.95rem;
            line-height: calc(0.95rem * 1.45);
        }
        .footer-mail {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #000;
            font-size: 1.1rem;
            .mail-icon {
                margin-right: 0.85rem;
            }
        }
        .icons-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .footer-icon {
                color: #000;
                font-size: 1.125rem;
                margin: 0 1.25rem;
                cursor: pointer;
            }
        }
    }
    ul {
        margin-top: 2rem;
        justify-content: flex-end;
        display: flex;
        flex-direction: row;
        li {
            list-style: none;
            padding: 0 1.75rem;
            line-height: calc(1.5rem * 1.4);
        }
    }
}