.team-content {
    div {
        padding: 2.5vh 0 1.25vh;
        background-color: #F4F4F2;
        .team-image {
            display: flex;
            margin: 15vh auto;
            width: 82.5%;
            height: 70vh;
            object-fit: cover;
            border-radius: 50px;
            filter: brightness(0.375);
            object-position: bottom;
        }
        .team-image_description {
            background-color: transparent;
            color: #fff;
            text-align: center;
            position: absolute;
            top: 55%;
            left: 50%;
            transform: translate(-50%, -50%);
            user-select: none;
            h4 {
                font-weight: 400;
                font-size: 5.25rem;
            }
            p {
                margin: 1.25rem 0;
                font-size: 1.3rem;
            }
        }
    }
    .founders {
        background-color: #fff;
        margin: 17.5vh auto 15vh;
        width: 82.5%;
        display: flex;
        justify-content: space-between;
        .gsap-animation {
            opacity: 0;
            transform: translateY(100px);
        }
        aside {
            width: 40%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            h2 {
                font-weight: 400;
                font-size: 4.75rem;
                margin: 2rem 0;
            }
            p {
                margin: 1rem 0;
                font-size: 1.2rem;
                line-height: calc(1.2rem * 1.25);
            }
        }
        .founders-image {
            opacity: 0;
            width: 50%;
            height: 70vh;
            object-fit: cover;
            border-radius: 40px;
        }
    }
}